<template lang='pug'>
    div.header
        .header-logo
            i.el-icon-menu
        .header-title
            router-link(to='/home')
                p.header-title__spec GER
                    span MY
        .header-notice
            ul
                li.header-notice__list: a.header-notice__list_link
                    el-badge.item(:value='2')
                        img.btn-img(src='../../assets/remind.svg')
                li.header-notice__list: a.header-notice__list_link
                    el-badge.item(:value='3')
                        img.btn-img(src='../../assets/email.svg')
                li.header-notice__list: a.header-notice__list_link
                    el-badge.item(:value='11')
                        img.btn-img(src='../../assets/service.svg')

        .header-nav
            .people-icon
                img(src='http://thevectorlab.net/flatlab/img/avatar1_small.jpg')
            el-dropdown
                span.el-dropdown-link {{currentUser}}
                    i.el-icon-arrow-down.el-icon--right
                el-dropdown-menu(slot='dropdown')
                    router-link(to='/')
                        el-dropdown-item 退出

</template>
<script>
export default {
    data () {
        return {
            currentUser: ''
        }
    },
    created () {
        this.currentUser = localStorage.gm_username
    },
    methods: {
        // sidebarToggle () {
        //     this.$store.commit('collToggle')
        //     console.log(this.$store.state.collapse)
        // }
    }
}
</script>
<style scoped>
    .header {
        position: fixed;
        width: 100%;
        top: 0;
        z-index: 1000;
        background: #fff;
        height: 60px;
        border-bottom: 1px solid #f1f2f7;
        box-sizing: border-box;
        padding: 0 15px;
        display: flex;
        align-items: center;
    }
    .header-logo > i {
        font-size: 30px;
    }
    .header-logo > i:hover {
        cursor: pointer;
        color: rgb(58, 63, 70);
    }
    .header-notice {
        margin-left: 80px
    }
    .header-notice__list {
        display: inline-block;
    }
    .header-notice__list_link {
        display: block;
        padding: 3px 6px;
        border: 1px solid #f0f0f0;
        border-radius: 4px;
        margin-right: 10px;
        width: 16px
    }
    .header-notice__list_link:hover {
        cursor: pointer;
    }
    .btn-img {
        width: 100%;
    }
    .header-title__spec {
        margin-left: 10px;
        font-size: 25px;
        color: #2e2e2e;
    }
    .header-title__spec > span {
        color: #FF6C60;
    }
    .header-nav {
        margin: 0 auto;
        margin-right: 0;
        border: 1px solid #f0f0f8;
        padding: 6px;
        border-radius: 4px;
        color: #666666;
        font-size: 16px;
        display: flex;
        align-items: center;
    }
    .el-dropdown-link {
        margin-left: 10px;
        cursor: pointer;
    }
</style>



