<template lang='pug'>
    .sidebar
        el-menu.el-menu-vertical-demo(default-active='/home', background-color='#2a3542', text-color='#fff', active-text-color='#ffd04b', unique-opened=true, router=true)
            el-menu-item(index='/home')
                i.el-icon-star-on
                span(slot='title') 首页
            el-submenu(index='1')
                template(slot='title')
                    i.el-icon-upload
                    span 机票
                el-menu-item(index='/flight') 机票查询
                el-menu-item(index='/flightContact') 机票预定
            el-submenu(index='2')
                template(slot='title')
                    i.el-icon-location
                    span 酒店
                el-menu-item(index='/hotel') 酒店查询
                el-menu-item(index='/2-2') 酒店订单2
            el-submenu(index='3')
                template(slot='title')
                    i.el-icon-document
                    span 订单
                el-menu-item(index='/order') 订单查询
            el-submenu(index='4')
                template(slot='title')
                    i.el-icon-bell
                    span 用户管理
                el-menu-item(index='/users') 用户列表
                el-menu-item(index='/4-2') 用户1
                el-menu-item(index='/4-3') 用户2
            el-submenu(index='5')
                template(slot='title')
                    i.el-icon-message
                    span 发票管理
                el-menu-item(index='/invoice') 发票
                el-menu-item(index='/5-2') 发票2
                el-menu-item(index='/5-3') 发票3
            el-submenu(index='6')
                template(slot='title')
                    i.el-icon-star-on
                    span 航意险管理
                el-menu-item(index='/insurance') 航意险
                el-menu-item(index='6-2') 航意险2
                el-menu-item(index='6-3') 航意险3
            el-submenu(index='7')
                template(slot='title')
                    i.el-icon-printer
                    span 返现提现
                el-menu-item(index='/return') 返现
                el-menu-item(index='7-2') 返现2
                el-menu-item(index='7-3') 返现3

</template>

<script>
export default {
    data () {
        return {
            theme2: 'light'
        }
    },
    computed: {
        // collapse () {
        //     return this.$store.state.collapse
        // }
    },
    created () {
        // console.log(store.state.collapse)
    }
}
</script>

<style scoped>
    .sidebar {
        position: fixed;
        top: 60px;
        bottom: 0;
        left: 0;
        width: 200px;
        border-right: 1px solid #2a3542;
        background-color: #2a3542;
        z-index: 100;
    }
</style>



